import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    todos: [
      {id: 1, text: '111', done: true},
      {id: 2, text: '222', done: false}
    ],
    currentUserId: -1,
    userName: '',
    loginName: '',
    currentAId: -1
  },
  mutations: {
    changeText (state, text) {
      state.todos[0].text = text
    },
    setCurrentUserId (state, userId) {
      state.currentUserId = userId
      window.sessionStorage.setItem('userId', userId)
    },
    setUserName (state, username) {
      state.userName = username
      window.sessionStorage.setItem('userName', username)
    },
    setLoginName (state, loginName) {
      state.loginName = loginName
      window.sessionStorage.setItem('loginName', loginName)
    },
    setCurrentAId (state, id) {
      state.currentAId = id
      window.sessionStorage.setItem('aid', id)
    }
  },
  getters: {
    doneTodos: state => {
      return state.todos.filter(doto => doto.done)
    },
    currentUserId (state) {
      if (state.currentUserId === -1) {
        state.currentUserId = window.sessionStorage.getItem('userId')
      }
      return state.currentUserId
    },
    userName (state) {
      if (!state.userName) {
        state.userName = window.sessionStorage.getItem('userName')
      }
      return state.userName
    },
    loginName (state) {
      if (!state.loginName) {
        state.loginName = window.sessionStorage.getItem('loginName')
      }
      return state.loginName
    },
    aid (state) {
      if (state.cuurentAId === -1) {
        state.currentAId = window.sessionStorage.getItem('aid')
      }
      return state.currentAId
    }
  }
})



