import Vue from 'vue'
import Router from 'vue-router'
// import App from '../App'

Vue.use(Router)

const routes = [
  {
    path: '/',
    component: reslove => require.ensure([], () => reslove(require('../pages/login/login')), 'login')
  },
  {
    path: '/main',
    name: 'main',
    component: reslove => require.ensure([], () => reslove(require('../pages/main/main')), 'main')
  },
  {
    path: '/article',
    name: 'article',
    component: reslove => require.ensure([], () => reslove(require('../pages/article/article')), 'article')
  },
  {
    path: '/create_type',
    name: 'create_type',
    component: reslove => require.ensure([], () => reslove(require('../pages/create/type')), 'create_type')
  },
  {
    path: '/create_article',
    name: 'create_article',
    component: reslove => require.ensure([], () => reslove(require('../pages/create/article')), 'create_article')
  }]

export default new Router({
  routes
})



